import noData from '@/components/noData'
import back from '@/components/back'

export default {
    components:{
        noData,
        back
    },
    data(){
        return {
            page: 1,
            haveMore: true,
            list: [],
            user: this.$store.state.userInfo.Data,
            pageShow: false,
            sorList: ['全部服务项', '全部订单'],
            index: null,
            allServerList: [],
            allOrderList: [
                {
                    name: '全部订单',
                    val: '-1'
                },
                {
                    name: '已支付',
                    val: '2'
                },
                {
                    name: '已退款',
                    val: '5'
                }
            ],
            allServer: {
                name: '全部服务项',
                val: '0'
            },
            allOrder: {
                name: '全部订单',
                val: '-1'
            }
        }
    },
    created(){
        this.pull(false)
    },
    mounted(){
        this.getMore()
        this.getService()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods:{
        chose(i) {
            console.log(i)
            this.allServer = i
            this.sorList[this.index] = i.name
            this.index = null
            this.haveMore = true
            this.page = 1
            this.pull(false)
        },
        chose1(i) {
            this.allOrder = i
            this.sorList[this.index] = i.name
            this.index = null
            this.haveMore = true
            this.page = 1
            this.pull(false)
        },
        sort(idx) {
            if(this.index == idx) {
                this.index = null
            } else {
                this.index = idx
            }
        },
        goDetail (item) {
            this.$router.push(`/server-order-detial?id=${item.PayOrderId}`)
        },
        pull(h){
            if(this.haveMore){
                this.$post('/PlatFormAPI/PayOrder/QueryDoctorPackPayOrderPage',{
                    DrId: this.user.User.DoctorId,
                    ServiceItemId: this.allServer.val,
                    PayStatus: this.allOrder.val,
                    PageIndex: this.page,
                    PageSize: 15
                }).then(o => {
                    this.pageShow = true
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    
                    if (h){
                        o.ReturnData.forEach(v => {
                            this.list.push(v)
                        })
                    }
                    else {
                        this.list = o.ReturnData
                    }
                })
            }
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        },
        getService () {
            this.$get('/PlatFormAPI/PayOrder/QueryDoctorServiceTerm', {
                DrId: this.user.User.DoctorId,
                DrType: 1
            }).then( m => {
                this.allServerList = m.map((item, index) => {
                    return {
                        name: item.DoctorServiceWayName,
                        val: item.ServiceItemId
                    }
                })
            })
        }
    }
}



